@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
  letter-spacing: 1px;
}

body {
  background-color: #DDD;
}

.squarebutton {
  background-color: #9C909C;
  box-shadow: 3px 5px 15px 0px #303030;
  font-size: 2em;
  text-transform: uppercase;
  padding: 0.2em 1em;
  outline: none;
  border: none;
}

a:link, a:visited {
  text-decoration: none;
  color: #9F9F9F;
  outline: none;
}

a:hover {
  color: #A09381;
}

.App {
  height: 100vh;
}

.copyright__datestamp {
  text-transform: uppercase;
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #9F9F9F;
}

button:hover {
  cursor: pointer;
}
.landingpage__container {
    height: 80vh;
    display: grid;
    grid-template-columns: 1fr;
    place-items: center;
}

.landingpage__logo {
    width: 80%;
    align-self: end;
    max-width: 400px;
}

.landingpage__createbtn {
    margin: 0.5em;
    align-self: end;
}

.landingpage__p-wrapper {
    width: 80vw;
    max-width: 300px;
    align-self: start;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-evenly;
    text-transform: uppercase;
    color: #9F9F9F;
}

/* Position and sizing of burger button */
.bm-burger-button {
    position: fixed;
    width: 36px;
    height: 30px;
    left: 36px;
    top: 36px;
  }
  
  /* Color/shape of burger icon bars */
  .bm-burger-bars {
    background: #9C909C;
  }
  
  /* Color/shape of burger icon bars on hover*/
  .bm-burger-bars-hover {
    background: #A09381;
  }
  
  /* Position and sizing of clickable cross button */
  .bm-cross-button {
    height: 24px;
    width: 24px;
  }
  
  /* Color/shape of close button cross */
  .bm-cross {
    background: #bdc3c7;
  }
  
  /*
  Sidebar wrapper styles
  Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
  */
  .bm-menu-wrap {
    position: fixed;
    height: 100%;
  }
  
  /* General sidebar styles */
  .bm-menu {
    background: #373a47;
    padding: 2.5em 1.5em 0;
    font-size: 2em;
  }
  
  /* Morph shape necessary with bubble or elastic */
  .bm-morph-shape {
    fill: #373a47;
  }
  
  /* Wrapper for item list */
  .bm-item-list {
    color: #dddddd;
    padding: 0.8em;
}

/* Individual item */
.bm-item {
    display: inline-block;
    color: #dddddd;
    padding-left: 1em;
    padding-bottom: 0.3em;
    border-left: 3px solid #ddd;
  }
  
  /* Styling of overlay */
  .bm-overlay {
    background: rgba(0, 0, 0, 0.3);
  }
.ongoingpage__container {
    height: 100vh;
    display: flex;
    padding-top: 100px;
    padding-bottom: 50px;
    flex-flow: column nowrap;
    align-content: center;
}

.ongoingpage__createbtn {
    margin: 0.5em;
}

.ongoingpage__container > p {
    max-width: 70%;
    margin: 0 auto 1em auto;
    text-align: center;
    font-size: larger;
}

.ongoingpage__link {
    display: grid;
}

.ongoingpage__container > ul {
    margin: 0 auto;
    list-style: none;
    font-size: 1.5em;
}

.ongoingpage__container > h1 {
    font-size: 2em;
    padding-bottom: 1em;
    margin: 0 auto;
    text-transform: uppercase;
}

.landingpage__p-wrapper > p {
    padding: 0.2em;
}

.ongoingpage__recipe-row {
    padding-top: 1em;
    padding-bottom: 1em;
    font-size: 1.5em;
    display: grid;
    grid-template-columns: 40vw 40vw;
}

.ongoingpage__recipe-row:hover {
    color: #A09381;
}

.ongoingpage__recipe-row > a {
    justify-self: end;
}

.ongoingpage__recipe-row > button {
    justify-self: start;
    width: 30px;
    font-size: 1em;
    margin-left: 1em;
}
.newbread__breadid {
    position: fixed;
    right: 36px;
    top: 36px;
    text-transform: uppercase;
}

.newbread__container {
    height: 100vh;
    padding-top: 100px;
    padding-bottom: 50px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 50px 50px 30px auto 50px 50px;
    align-items: center;
}

.newbread__recipetitle {
    text-transform: uppercase;
    justify-self: center;
    grid-column: span 3;
}

#newbread__edit {
    font-size: 1em;
    grid-column: span 3;
    justify-self: center;
}

.newbread__hydration {
    grid-column: span 3;
    justify-self: center;
}

.newbread__ingredientlist {
    align-self: start;
    grid-column: span 3;
    display: grid;
    grid-template-columns: 2fr 1fr;
    align-items: start;
}

.newbread__ingredient-list {
    grid-column: span 2;
}

.newbread__flours {
    margin-left: 1.5em;
}

.newbread__grams {
    justify-self: start;
}

.createbread__link {
    grid-column: span 3;
    display: grid;
    place-items: center;
}

.createbread__continue, .createbread__store {
    place-self: start center;
    font-size: 1.5em;
    width: 200px;
}

.createbread__continue {
    grid-column: span 3;
}

.createbread__store {
    background-color: #DDD;
    box-shadow: 3px 5px 15px 0px #303030;
    text-transform: uppercase;
    padding: 0.2em 1em;
    outline: none;
    border: 3px solid #9C909C;
}

form {
    grid-column: span 3;
    justify-self: center;
}

#ingredient-submitbtn {
    font-size: 1em;
}

select {
    padding: 0.2em;
    margin: 0.5em;
}

.newbread__error {
    color: red;
    text-align: center;
}

.newbread__one-ingredient-row {
    display: grid;
    grid-template-columns: 0.2fr 2fr 1fr;
    margin: 0.1em 1.5em;
}

.newbread__one-ingredient-row__grams {
    align-self: start;
    width: 80px;
}

.newbread__one-ingredient-row__deletebtn {
    margin-right: 0.5em;
    background-color: red;
    font-weight: 800;
}
.submitbread__container {
    height: 100vh;
    padding-top: 100px;
    padding-bottom: 50px;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 80px 50px 50px 80px auto 50px 80px;
    place-items: center;
}

.submitbread__title {
    text-transform: uppercase;
}

.submitbread__ratingcontainer {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    place-items: center;
}

.submitbread__ratingcontainer > * {
    font-size: 3em;
}

.submitbread__ratetext {
    align-self: start;
}

textarea {
    place-self: start center;
}

.submitbread__continue {
    place-self: center;
    font-size: 1.5em;
    width: 200px;
}
.recipebank__container {
    height: 100vh;
    padding-top: 100px;
    padding-bottom: 50px;
    display: flex;
    flex-flow: column wrap;
    /* justify-content: center; */
    align-items: center;
    align-content: center;

}
.recipebank__container > h1 {
    text-transform: uppercase;
}

.recipebank__container > ul {
    list-style: none;
    display: grid;
}

.recipebank__container > ul > li {
    padding-top: 1em;
    padding-bottom: 1em;
    font-size: 1.5em;
    display: grid;
    grid-template-columns: 40vw 40vw;
}

.recipebank__container > ul > li:hover {
    color: #A09381;
}

.recipebank__container > ul > li > h2 {
    justify-self: end;
}

.recipebank__container > ul > li > button {
    justify-self: start;
    width: 30px;
    font-size: 1em;
    margin-left: 1em;
}
.flourlist__wrapper {
    height: 100vh;
    padding-top: 100px;
    padding-bottom: 80px;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 80px 60px 50px auto;
    place-items: center;
}

.flourlist__wrapper > h1 {
    text-transform: uppercase;
}

.addflour-btn {
  background-color: #9C909C;
  box-shadow: 3px 5px 15px 0px #303030;
  font-size: 1em;
  text-transform: uppercase;
  padding: 0.2em 1em;
  outline: none;
  border: none;
}

#flourlist__form {
    grid-column: span 1;
}

.flourlist__listcontainer {
    align-self: start;
    margin-top: 1em;
    overflow-y: scroll;
    max-height: 280px;
}

.flourlist__listcontainer > ul {
    list-style: none;
}

.flourlist__listcontainer > ul > li {
    padding-top: 1em;
    padding-bottom: 1em;
    font-size: 1.5em;
    display: grid;
    grid-template-columns: 50vw 30vw;
}

.flourlist__listcontainer > ul > li:hover {
    color: #A09381;
}

.flourlist__listcontainer > ul > li > h3 {
    justify-self: end;
    font-size: 0.7em;
}

.flourlist__listcontainer > ul > li > button {
    place-self: center start;
    width: 30px;
    font-size: 0.7em;
    margin-left: 1em;
}
