.submitbread__container {
    height: 100vh;
    padding-top: 100px;
    padding-bottom: 50px;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 80px 50px 50px 80px auto 50px 80px;
    place-items: center;
}

.submitbread__title {
    text-transform: uppercase;
}

.submitbread__ratingcontainer {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    place-items: center;
}

.submitbread__ratingcontainer > * {
    font-size: 3em;
}

.submitbread__ratetext {
    align-self: start;
}

textarea {
    place-self: start center;
}

.submitbread__continue {
    place-self: center;
    font-size: 1.5em;
    width: 200px;
}